import { useEffect, useState } from 'react';
import classNames from 'classnames/bind'

import * as productService from '../../services/productService'
import * as categoryService from '../../services/categoryService'

import setTitle from '../../utils/setTitle';
import CategoryItem from '../../components/Category';
import ProductItem from '../../components/Product';
import Container from '../../components/Container';
import Group from '../../components/Group';
import Page from '../../components/Page';
import styles from './index.module.scss'
import Slider from '../../components/Slider';

const cx = classNames.bind(styles)

function Home({ title }) {
    setTitle(title)

    const [categories, setCategories] = useState([])
    const [products, setProducts] = useState([])

    useEffect(() => {
        const fetchApi = async () => {
            try {
                const arrayCategories = await categoryService.getCategories()
                setCategories(arrayCategories)
            } catch (e) {

            }
        }
        fetchApi()
    }, [])

    useEffect(() => {
        const fetchApi = async () => {
            try {
                const arrayProducts = await productService.getProducts()
                setProducts(arrayProducts)
            } catch (e) {

            }
        }
        fetchApi()
    }, [])

    return (
        <Page>
            {/* <div className={cx('carousel')}>
                <ul>
                    <li>Dng API t <a href="https://api-nghiquan.herokuapp.com/">https://api-nghiquan.herokuapp.com/</a> (b t build bng nodejs)</li>
                    <li>React slick g vl, bug nh slide TOP SALES OFF, ngi khong 20s l thy ci cnh ngu ca slick react library, gii php l s lng sn phm phi chia ht cho s s dng slide</li>
                    <li>Hi nn nn ri , nhng c thi, qu c cng nguyn</li>
                </ul>
            </div> */}

            <div className={cx('carousel')}>
                <Container>
                    <Slider className={cx('slider')} setting={{ fade: true, autoplaySpeed: 7000, speed: 2000, }}>
                        {
                            categories.map((item, index) => (
                                <div key={index} className={cx('slide')}>
                                    <img className={cx('image')} src={item.image} alt={item.name} />
                                </div>
                            ))
                        }
                    </Slider>
                </Container>
            </div>

            <Container>
                <Group title='top sn phm mi nht' classNameTitle={cx('title')}>
                    <Slider setting={{ slidesToShow: 5, rows: 1, }}>
                        {
                            products.map((item, index) => (
                                <ProductItem key={index} data={item} />
                            ))
                        }
                    </Slider>
                </Group>
            </Container>

            <Container>
                <Group title='top Sales off' column="1">
                    <Slider setting={{ slidesToShow: 4, rows: 2, autoplaySpeed: 5000, }}>
                        {
                            products.map((item, index) => (
                                <ProductItem key={index} data={item} />
                            ))
                        }
                    </Slider>
                </Group>
            </Container>

            <Container className={cx('container')}>
                <Group className={cx('category-sidebar')} title='Danh mc' column="1" space="4">
                    {
                        categories.map((item, index) => (
                            <CategoryItem key={index} data={item} />
                        ))
                    }
                </Group>
                <Group className={cx('product-list')} classNameTitle={cx('title')} title="Gi  cho bn" column="4">
                    {
                        products.map((item, index) => (
                            <ProductItem key={index} data={item} />
                        ))
                    }
                </Group>
            </Container>
        </Page >
    );
}

export default Home;